import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MuiLink from '@material-ui/core/Link';
import ProTip from '~/src/ProTip';
import Link from '~/src/link';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import ListAltIcon from '@material-ui/icons/ListAlt';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import EmailIcon from '@material-ui/icons/Email';
import MonetizationIcon from '@material-ui/icons/MonetizationOnRounded';

import Header from '~/src/header';
import Footer from'~/src/footer';
import { maxWidth } from '@material-ui/system';
import { useTranslatorContext } from '~/src/utility/translator/translatorContext';

const useStyles = makeStyles(theme => ({
  paper: {
    padding: 16
  },
  avatarContainer: {
    position: "relative",
    boxShadow: "0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)",
    paddingTop: "25%",
    [theme.breakpoints.up('md')]: {
      paddingTop: '100%',
    },
    display: "flex",
    justifyContent: "center",
  },
  avatar: {
    position: "absolute",
    width: "25%",
    height: "100%",
    [theme.breakpoints.up('md')]: {
      width: "100%",
    },
    top: 0,
    objectFit: "contain"
  },
  labelTricker: {
    flexDirection: "column",
  }
}));

// https://documentation.laborator.co/kb/kalium/creating-about-me-page/
// https://www.pinterest.com/pin/153966880985437642
export default function Index() {
  const classes = useStyles({});
  const t = useTranslatorContext();

  return (
    <React.Fragment>
      <CssBaseline />
      <Header tabIndex={5}/>
      <main>
        <Container style={{paddingTop: 24, minHeight: "90vh"}}>
          <Typography variant="h4" gutterBottom>
            {t.about.about_software}
          </Typography>
          <Box height={16} />
          <Grid container spacing={3}>
            <Grid item md={4} sm={12} xs ={12}>
              <Box className={classes.avatarContainer} style={{boxShadow: "none",}}>
                <img className={classes.avatar} src="/images/about/minecraft_scepter.png" style={{boxShadow: "0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)"}}/>
              </Box>
            </Grid>
            <Grid item md={6} sm={12} xs ={12}>
              <Container className={classes.paper}>
                  <Typography variant="h5" gutterBottom>
                    {t.about.minecraft_scepter_title}
                  </Typography>
                  <Box style={{borderRadius: 16, border: "3px solid black"}} />
                  <Box height={16}/>
                  <Typography variant="h6" gutterBottom style={{color: "#00000044", fontStyle: "italic"}}>
                  {t.about.minecraft_scepter_desc_a}
                  </Typography>
                  <Box height={16}/>
                  <Typography variant="h6" gutterBottom style={{color: "#2B2B2B"}}>
                    <u>{t.about.minecraft_scepter_desc_b}</u>{t.about.minecraft_scepter_desc_c}<b>{t.about.minecraft_scepter_desc_d}</b>{t.about.minecraft_scepter_desc_e}<i>{t.about.minecraft_scepter_desc_f}</i>{t.about.minecraft_scepter_desc_g}
                  </Typography>
              </Container>
            </Grid>
            <Grid item md={2} sm={12} xs={12}>
              <IconButton 
                onClick={
                  () => window.open("https://www.youtube.com/channel/UCWx2R78GhmltZudJHLEnd5w", '_blank')
                }>
                  <Typography variant="subtitle1">
                    {t.about.yt_channel}
                  </Typography>
                  <Box width={4}/>
                  <PlayCircleFilledIcon fontSize="large" style={{color: "#F9000B"}}/>
              </IconButton>
              <Container className={classes.paper} style={{padding: 0}}>
                <IconButton onClick={
                  () => window.open("https://docs.google.com/forms/d/e/1FAIpQLSd0ObxdeGqnurQ4GOpqmaIFXYGmg1jh4W_1Y-DcTsjBonuxew/viewform?usp=sf_link", '_blank')
                }>
                  <Typography variant="subtitle1">
                    {t.about.form}
                  </Typography>
                  <Box width={4}/>
                  <ListAltIcon fontSize="large"/>
                </IconButton>
              </Container>
              <IconButton 
                onClick={
                  // () => window.open("https://p.ecpay.com.tw/8F8FE", '_blank')
                  () => window.open("http://bit.ly/minecraftscepterdonation_en", '_blank')
                }>
                  <Typography variant="subtitle1">
                    {t.about.support}
                  </Typography>
                  <Box width={4}/>
                  <MonetizationIcon fontSize="large" style={{color: "#FFBF00"}}/>
              </IconButton>
            </Grid>
          </Grid>
        </Container>
        <Box height={24}/>
        <Container style={{
          backgroundColor: "#EAEAEA",
          boxShadow: "inset 0px -4px 8px -5px #000, inset 0px 4px 8px -5px #000",
          paddingBottom: 36,
          paddingTop: 36,
          minHeight: "90vh",
          maxWidth: "100%"
        }}>
          <Container>
            <Typography variant="h4" gutterBottom>
              {t.about.about_author}
            </Typography>
            <Box height={16} />
            <Grid container spacing={3}>
              <Grid item md={6} sm={12} xs={12}>
                <Container className={classes.paper}>
                    <Typography variant="h5" gutterBottom>
                      {t.about.chinyun}
                    </Typography>
                    <Box style={{borderRadius: 16, border: "3px solid black"}} />
                    <Box height={16}/>
                    <Typography variant="h6" gutterBottom style={{color: "#00000044", fontStyle: "italic"}}>
                      {t.about.author_desc_a}
                    </Typography>
                    <Box height={16}/>
                    <Typography variant="h6" gutterBottom style={{color: "#2B2B2B"}}>
                      {t.about.author_desc_b}
                    </Typography>
                </Container>
              </Grid>
              <Grid item md={4} sm={12} xs={12}>
                <Box style={{padding: 24}}>
                  <Box className={classes.avatarContainer} style={{boxShadow: "none",}}>
                    <Avatar src="/images/about/about_me.png" className={classes.avatar} style={{boxShadow: "0 0 0 6px rgb(30,85,192), 0 2px 19px 6px #000"}} />
                    {/* <img className={classes.avatar} src="/images/about/about_me.png" /> */}
                  </Box>
                </Box>
              </Grid>
              <Grid item md={2} sm={12} xs={12}>
                <Container className={classes.paper} style={{padding: 0, display: "flex", justifyContent: "center"}}>
                  <Box height={16}/>
                  <IconButton 
                    classes={{label: `${classes.labelTricker}`}}
                    onClick={
                    () => window.open("mailto:tokenyete@gmail.com?subject=請問青雲...&body=內容")
                  }>
                    <Typography variant="subtitle1">
                      {t.about.author_mail}
                    </Typography>
                    <Typography variant="subtitle1">
                      tokenyete@gmail.com
                    </Typography>
                    <Box width={4}/>
                    <EmailIcon fontSize="large" style={{color: "rgb(82, 141, 238)"}}/>
                  </IconButton>
                </Container>
              </Grid>
            </Grid>
          </Container>
        </Container>
      </main>
      {/* Footer */}
      <Footer style={undefined}/>
      {/* End footer */}
    </React.Fragment>
  );
}
